.test-container {
  max-width: 600px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

.question-section {
  margin-bottom: 20px;
}

.question-count {
  font-weight: bold;
  margin-bottom: 10px;
}

.answer-section button {
  display: block;
  margin: 5px 0;
  padding: 10px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

.answer-section button:hover {
  background-color: #0056b3;
}

.score-section {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

